import uniDrawer from '../../component/uni-drawer/uni-drawer.vue'
export default {
	components: {
		uniDrawer
	},
	data() {
		return {
			showRight: false,
			showLeft: false
		}
	},
	methods: {
		show(e) {
			console.log("show", e);
			if (e === 'left') {
				this.showLeft = true
			} else {
				this.showRight = true
			}
		},
		hide() {
			console.log("hide");
			this.showLeft = false
			this.showRight = false
		},
		closeDrawer(e) {
			if (e === 'left') {
				this.showLeft = false
			} else {
				this.showRight = false
			}
		},
		confirm() {}
	},
	onNavigationBarButtonTap(e) {
		this.showRight = !this.showRight
	},
	onBackPress() {
		if (this.showRight || this.showLeft) {
			this.hide()
			return true
		}
	}
}
